$color-primary: #ff70d9;
$color-accent: #ebff00;
$color-accent--hover: #9cb300;
$color-bg: #304571;
$color-bg-header: #000000;
$color-bg-footer: #000000;
$color-text: #ffffff;
$font-size: 16px;
$z-header: 1000;

.app {
  --bg-color: #{$color-bg};
  --text-color: #{$color-text};

  background-color: var(--bg-color);
  color: var(--text-color);
}

.c-header {
  --bg-color: #{$color-bg-header};
  --text-color: #{$color-text};
  --height: 48px;
  --padding: 5px 20px;

  position: relative;
  z-index: #{$z-header};

  background-color: var(--bg-color);
  color: var(--text-color);

  height: var(--height);

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: var(--padding);

    @media screen and (min-width: 1248px) {
      --padding: 5px 0;
    }
  }
}

.c-nav {
  position: relative;
  &__mobile-checkbox {
    display: none;
  }
  &__mobile-hamburger {
    --size: 22px;
    --color: #{$color-text};

    width: var(--size);
    height: var(--size);

    display: flex;
    flex-direction: column;
    gap: 7px;

    &-line {
      width: var(--size);
      height: 2px;
      background-color: var(--color);
      transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              opacity 0.55s ease;
    }
    @media screen and (min-width: 768px) {
      display: none;
    }
  }
  &__mobile-checkbox:checked {
    + .c-nav__mobile-hamburger {
      .c-nav__mobile-hamburger-line:nth-child(1) {
        transform: translate(0px, 12px) rotate(45deg);
      }
      .c-nav__mobile-hamburger-line:nth-child(2) {
        opacity: 0;
        transform: scale(0);
      }
      .c-nav__mobile-hamburger-line:nth-child(3) {
        transform: translate(0px, -6px) rotate(-45deg);
      }
    }
    ~ .c-nav__list-wrapper {
      grid-template-rows: 1fr;
    }
  }
  &__list-wrapper {
    display: grid;
    grid-template-rows: 0fr;
    position: absolute;
    top: 100%;
    right: 0;
    width: max-content;
    overflow: hidden;
    transition: grid-template-rows 0.3s;

    @media screen and (min-width: 768px) {
      display: flex;
      position: static;
    }
  }
  &__list {
    display: flex;
    width: max-content;
    min-height: 0;
    background-color: #{$color-bg-header};
    padding: 5px 10px 0 5px;
    gap: 10px;

    flex-direction: column;
    align-items: flex-end;
    text-align: right;

    list-style: none;
    margin: 0;

    @media screen and (min-width: 768px) {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 0;
      gap: 30px;
      background-color: transparent;
    }
  }
  &__item {
    &:nth-child(3n + 1) {
      width: 100%;
    }
  }
  &__link {
    color: #{$color-text};
    text-decoration: none;
    transition: color 0.3s;
    font-size: 18px;
    font-weight: 700;

    &:hover {
      background-image: linear-gradient(
        98.25deg,
        #ebff00 18.56%,
        #ff70d9 76.02%
      );
      color: transparent;
      background-clip: text;
    }
  }
  &__icon-wrapper {
    position: relative;
    display: inline-block;
  }
  &__item &__icon {
    transition: opacity 0.3s;
    opacity: 1;
  }
  &__icon-hover {
    transition: opacity 0.3s;
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
  }
  &__item:hover &__icon {
    opacity: 0;
  }
  &__item:hover &__icon-hover {
    opacity: 1;
  }
}

.s-hero {
  --earth-position: center -125px;
  position: relative;
  background: url("~/src/assets/images/earth.png") no-repeat
      var(--earth-position),
    url("~/src/assets/images/hero-bg.jpg") no-repeat center 93%;

  @media screen and (min-width: 768px) {
    --gap: 22px;
    --earth-position: calc(100% + 100px) calc(50% + 20px);
    --padding: 100px 30px;
    background-size: 300px, cover;
    align-items: flex-start;
  }

  @media screen and (min-width: 1248px) {
    --earth-position: calc(100% - 80px) calc(50% + 20px);
    --padding: 90px 90px 120px 90px;
  }

  &__container {
    --padding: 250px 34px 44px 34px;
    --gap: 17px;
    --width-content: 400px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    gap: var(--gap);
    padding: var(--padding);
    text-align: center;
    position: relative;
    z-index: 1;

    @media screen and (min-width: 768px) {
      --gap: 22px;
      --padding: 100px 30px;
      align-items: flex-start;
    }

    @media screen and (min-width: 1248px) {
      --padding: 90px 0 120px 0;
      --width-content: 693px;
    }
  }

  &:after {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) 0%,
      rgba(15, 20, 32, 0.79) 100%
    );
  }

  &__title {
    --font-size: 36px;
    font-size: var(--font-size);
    line-height: 1;
    font-weight: 800;
    margin: 0;
    max-width: var(--width-content);

    &-accent {
      color: #{$color-primary};
    }

    @media screen and (min-width: 768px) {
      --font-size: 48px;
      text-align: left;
    }

    @media screen and (min-width: 1248px) {
      font-size: 72px;
    }
  }

  &__description {
    --font-size: 1rem;
    font-size: var(--font-size);
    font-weight: 800;
    margin: 0;
    max-width: var(--width-content);

    @media screen and (min-width: 768px) {
      --font-size: 22px;
    }

    @media screen and (min-width: 1248px) {
      --font-size: 32px;
    }
  }

  &__description-accent {
    color: #{$color-accent};
  }

  &__footer {
    display: flex;
    justify-content: center;
    max-width: var(--width-content);
  }
}

.s-main {
  &__container {
    --padding: 50px 20px;
    padding: var(--padding);

    @media screen and (min-width: 768px) {
      --padding: 55px 30px;
    }
    @media screen and (min-width: 1248px) {
      --padding: 58px 0;
    }
  }

  &__title {
    --font-size: 18px;
    font-size: var(--font-size);
    font-weight: 800;
    margin: 0;

    @media screen and (min-width: 768px) {
      --font-size: 24px;
    }

    @media screen and (min-width: 1248px) {
      --font-size: 32px;
    }
  }
  &__list {
    --margin-top: 30px;
    --gap: 30px;
    display: grid;
    gap: var(--gap);
    list-style: none;
    padding: 0;
    margin: var(--margin-top) 0 0;

    @media (min-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (min-width: 1248px) {
      --margin-top: 40px;
      --gap: 40px;
    }
  }
  &__list-item {
    @media (min-width: 1248px) {
      &:nth-child(3n + 1) {
        grid-column: span 2;
      }
    }
  }
  &__item {
    --ui-card-padding: 0 27px 32px 27px;

    container-type: inline-size;

    overflow: hidden;
    position: relative;

    display: flex;
    flex-direction: column;
    gap: 22px;
    justify-content: flex-end;

    @media screen and (min-width: 768px) {
      --ui-card-padding: 0 25px 25px 25px;
    }

    @media screen and (min-width: 1248px) {
      --ui-card-padding: 0 35px 35px 35px;
    }

    > * {
      z-index: 1;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: 0;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(15, 20, 32, 0.79) 100%
      );
    }
  }
  &__item-image {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__item-title {
    --font-size: 40px;
    --margin-top: 100px;
    --max-width: 100%;
    position: relative;
    font-size: var(--font-size);
    font-weight: 800;
    line-height: 1;
    margin: var(--margin-top) 0 0 0;
    max-width: var(--max-width);

    @media screen and (min-width: 768px) {
      --margin-top: 105px;
      --max-width: 6em;
    }

    @media screen and (min-width: 1248px) {
      --font-size: 46px;
      --margin-top: 50px;
      --max-width: 6.2em;

      @container (min-width: 600px) {
        --font-size: 74px;
        --margin-top: 35px;
        --max-width: 9em;
      }
    }
  }
  &__item-description {
    --font-size: 1rem;
    font-size: var(--font-size);
    position: relative;
    font-weight: 800;
    margin: 0;

    @media screen and (min-width: 1248px) {
      --font-size: 24px;
    }

    @container (min-width: 600px) {
      --font-size: 26px;
    }
  }
  &__item-footer {
    position: relative;
    display: flex;
  }

  &__item-last {
    --margin-top: 50px;
    margin-top: var(--margin-top);

    @media screen and (min-width: 1248px) {
      --margin-top: 62px;
    }

    &-title {
      --font-size: 18px;
      font-size: var(--font-size);
      font-weight: 800;
      margin: 0;

      @media screen and (min-width: 768px) {
        --font-size: 24px;
      }

      @media screen and (min-width: 1248px) {
        --font-size: 32px;
      }
    }
    &-description {
      --font-size: 14px;
      --margin-top: 20px;
      opacity: 0.6;
      font-size: var(--font-size);
      font-weight: 700;
      margin: var(--margin-top) 0 0 0;

      @media screen and (min-width: 768px) {
        --font-size: 16px;
        --margin-top: 13px;
      }

      @media screen and (min-width: 1248px) {
        --font-size: 20px;
        --margin-top: 17px;
      }
    }
    &-footer {
      --margin-top: 20px;
      margin-top: var(--margin-top);

      @media screen and (min-width: 1248px) {
        --margin-top: 17px;
      }
    }
  }
}

.c-footer {
  --bg-color: #{$color-bg-footer};
  --text-color: #{$color-text};

  background-color: var(--bg-color);
  color: var(--text-color);

  &__container {
    padding-bottom: 20px;
  }

  &__image-wrapper {
    display: flex;
    justify-content: center;
    margin-bottom: -40px;
  }
  &__content {
    font-size: 18px;
    line-height: 28px;
    font-weight: 800;
    text-align: center;
    color: #{$color-accent};
  }
}

.ui-card {
  --padding: var(--ui-card-padding, 0);
  --border-radius: 10px;
  --bg-color: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(15, 20, 32, 0.79) 100%
  );
  --text-color: #{$color-text};

  padding: var(--padding);
  border-radius: var(--border-radius);
  color: var(--text-color);
  background-size: contain;
  background-position: left top;
  background-image: var(--bg-image, none);

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: var(--border-radius);
    background-color: var(--bg-color);
    z-index: -1;
  }
}

.ui-button {
  --padding: 7px 40px;
  --border-radius: 25px;
  --font-size: 16px;
  --font-weight: 700;

  font-size: var(--font-size);
  font-weight: var(--font-weight);
  padding: var(--padding);
  border-radius: var(--border-radius);
  transition: background-color 0.3s, color 0.3s;
  cursor: pointer;

  background-color: var(--bg-color, #{$color-accent});
  color: var(--text-color, black);
  border: var(--border, none);
  text-decoration: none;

  @media screen and (min-width: 1248px) {
    --font-size: 22px;
    --padding: 14px 40px;
    --border-radius: 100px;
  }

  &--solid {
    --bg-color: #{$color-accent};
    --text-color: black;
    --border: none;

    &:hover {
      --bg-color: #{$color-accent--hover};
    }
  }

  &--outline {
    --bg-color: transparent;
    --text-color: #{$color-accent};
    --border: 2px solid currentColor;

    &:hover {
      --text-color: #{$color-accent--hover};
    }
  }

  &--text {
    --padding: 0;
    --border-radius: 0;
    --font-size: 14px;
    --bg-color: transparent;
    --text-color: #{$color-accent};
    --border: none;
    --font-weight: 800;

    &:hover {
      --text-color: #{$color-accent--hover};
    }

    @media screen and (min-width: 768px) {
      --font-size: 16px;
    }

    @media screen and (min-width: 1248px) {
      --font-size: 20px;
    }
  }

  &--size-lg {
    @media screen and (min-width: 768px) {
      --padding: 14px 40px;
      --font-size: 22px;
      --border-radius: 100px;
    }
  }
}

.ui-container {
  max-width: 1088px;
  margin-left: auto;
  margin-right: auto;
}
